.btn {
  position: relative;
  text-decoration: none;
  font-weight: 600;
  display: inline-block;
  padding: 0.55em 2.2em;
  width: fit-content;
  margin: 2px;
  cursor: pointer;
  border-radius: 15px;
}
.main .btn:hover,
.main .btn:active {
  opacity: 1;
}

.btn::before,
.btn::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
}

.btn::before {
  background: linear-gradient(200deg, #e05976, rgba(49, 61, 236, 0.8392156863));
  z-index: -2;
  padding: 3px;
  top: -3px;
  left: -3px;
  box-sizing: content-box;
  border-radius: 15px;
}

.btn::before {
  z-index: -1;
}

.btn::after {
  background-color: #f6f6f6;
  z-index: -1;
  top: 0;
  left: 0;
  border-radius: 12px;
  transform: scale(1);
  transform-origin: 50%;
  transition-property: transform;
  transition-duration: 0.3s;
  transition-timing-function: ease-out;
}

.bg-black .btn::after {
  background-color: #000000;
}

.btn:hover::after,
.btn:active::after {
  z-index: -1;
  transform: scale(95%, 80%);
  border-radius: 12px;
}

.btn:active {
  box-shadow: 0 0 10px #000;
}

.hamburger__checkbox {
  display: none;
}
.hamburger__button {
  display: block;
  height: 30px;
  aspect-ratio: 1/1;
  z-index: 99;
  cursor: pointer;
}
.hamburger__icon {
  position: relative;
  display: block;
  top: 50%;
}
.hamburger__icon, .hamburger__icon::before, .hamburger__icon::after {
  width: 30px;
  height: 2px;
  background-color: black;
  transition-duration: 0.5s;
  transition-timing-function: ease-in-out;
  border-radius: 20px;
}
.hamburger__icon {
  transition-duration: 0.1s;
}
.hamburger__icon::before, .hamburger__icon::after {
  content: "";
  position: absolute;
  left: 0;
  transition: all 0.8s;
}
.hamburger__icon::before {
  top: -9px;
}
.hamburger__icon::after {
  top: 9px;
}
.hamburger__checkbox:checked + .hamburger__button .hamburger__icon {
  background-color: transparent;
}
.hamburger__checkbox:checked + .hamburger__button .hamburger__icon::before {
  top: 0;
  transform: rotate(45deg);
}
.hamburger__checkbox:checked + .hamburger__button .hamburger__icon::after {
  top: 0;
  transform: rotate(-45deg);
}

.single-artikel .hamburger__icon, .single-artikel .hamburger__icon::before, .single-artikel .hamburger__icon::after {
  background-color: #fff;
}

@media screen and (min-width: 981px) {
  .animation {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    z-index: -1;
  }
  .animation .point {
    position: absolute;
    border-radius: 50%;
    transition-property: all;
    transition-duration: 3s;
    transition-timing-function: ease-out;
  }
  .animation .point1 {
    filter: blur(300px);
    width: 200px;
    height: 200px;
    background-color: #e05976;
    opacity: 0;
    animation: change_color 5s linear 1s infinite alternate, change_size 5s ease-in-out 1s infinite alternate, move 10s ease-in-out 5s infinite alternate, fade 1s forwards 9s;
  }
  .animation .point2 {
    filter: blur(300px);
    width: 200px;
    height: 200px;
    opacity: 0;
    background-color: rgba(49, 61, 236, 0.8392156863);
    animation: change_color 5s linear 1s infinite alternate-reverse, change_size 10s ease-in-out 1s infinite alternate-reverse, move 10s ease-in-out 1s infinite alternate-reverse, fade 1s forwards 9s;
  }
  @keyframes change_color {
    0% {
      background-color: #e05976;
    }
    100% {
      background-color: rgba(49, 61, 236, 0.8392156863);
    }
  }
  @keyframes change_size {
    0% {
      height: 200px;
      width: 700px;
      filter: blur(150px);
    }
    100% {
      height: 700px;
      width: 200px;
      filter: blur(100px);
    }
  }
  @keyframes move {
    0% {
      transform: rotate(0deg) translate(-300) rotate(0);
    }
    100% {
      transform: rotate(360deg) translate(-300px) rotate(-360deg);
    }
  }
  @keyframes fade {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 0.3;
    }
  }
}
